.partners__slider, .award__slider {
    height: 150px;
    position: relative;

    & .slick-dots {
        display: flex;
        justify-content: center;
        bottom: 5px;
        left: 0;
        right: 0;
        position: absolute;
    }

    & .slick-dots li {
        list-style: none;
        margin: 0 9px;
    }

    & .slick-dots button {
        font-size: 0;
        width: 16px;
        height: 16px;
        background-color: #E6E6E6;
        border-radius: 50%;
    }

    & .slick-dots li.slick-active button {
        width: 20px;
        height: 20px;
        background-color: #4579CB;
    }
}

.slick-track {
    display: flex;
    justify-content: space-between;
    padding-top: 42px;
}

.slick-list {
    overflow: hidden;
}

